body {
  width: 100vw;
  height: 100vh;
  margin: 0px;
  padding: 0px;
}

#root {
  width: 100vw;
  height: 100vh;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: black;
}

.app {
  --width: 1280px;
  --height: 720px;
  
/*  --ratio: calc(90vmin / var( --width)); */
  --ratio: 0.75;

  height: var(--height);
  width: var(--width);
  -webkit-transform: scale(var(--ratio));
      -ms-transform: scale(var(--ratio));
          transform: scale(var(--ratio));
}


#lid {
  -webkit-animation: blink 2.4s infinite;
          animation: blink 2.4s infinite;
}
#eye {
  -webkit-animation: squeeze 2.4s infinite;
          animation: squeeze 2.4s infinite;
}

@-webkit-keyframes blink {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(15px) scaleY(0);
            transform: translateY(15px) scaleY(0)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}

@keyframes blink {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(15px) scaleY(0);
            transform: translateY(15px) scaleY(0)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}
@-webkit-keyframes squeeze {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(3px) scaleY(0.8);
            transform: translateY(3px) scaleY(0.8)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}
@keyframes squeeze {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(3px) scaleY(0.8);
            transform: translateY(3px) scaleY(0.8)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}


